<template>
  <div class="page">
    <div class="page-content withdraw-result">
      <div class="submited" v-text="result_info">提现申请已提交</div>

      <div class="msg">最快24小时内可到账</div>

      <div class="div-back" @click="backHome">
        <span class="span-back">返回首页</span>
      </div>

      <div class="div-withdraw" @click="back">
        <span class="span-withdraw" v-text="button_text">继续提现</span>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "../../../scss/mixins.scss";

  .withdraw-result {
    background: #FFFFFF;

    @include font-regular()

    .submited {
      position: absolute;
      top: 110px;
      left: 50%;
      margin-left: -100px;
      width: 200px;
      height: 33px;
      line-height: 33px;
      font-size: 24px;
      text-align: center;
    }

    .msg {
      position: absolute;
      top: 163px;
      left: 50%;
      margin-left: -64px;
      width: 128px;
      height: 18px;
      line-height: 18px;
      font-size: 13px;
      text-align: center;
    }

    .div-back {
      position: absolute;
      top: 301px;
      left: 50%;
      margin-left: -86px;
      width: 172px;
      height: 40px;
      border-radius: 2px;
      background: #ef502b;

      @include font-semibold()
    }

    .span-back {
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -9px;
      margin-left: -30px;
      width: 60px;
      height: 18px;
      font-size: 13px;
      color: #FFFFFF;
      text-align: center;
    }

    .div-withdraw {
      position: absolute;
      top: 351px;
      left: 50%;
      margin-left: -86px;
      width: 172px;
      height: 40px;
      border-radius: 2px;
      border: 1px solid #3A3A3A;
      color: #fff;

      @include font-semibold()
    }

    .span-withdraw {
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -9px;
      margin-left: -30px;
      width: 60px;
      height: 18px;
      font-size: 13px;
      color: #3A3A3A;
      text-align: center;
    }
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  export default {
    data () {
      return {
        button_text: '继续提现',
        result_info: '提现申请已提交'
      }
    },

    computed: {
      title () {
        let backroute = this.$route.params.backroute
        switch (backroute) {
          case 'rebatescore':
            return '集分宝兑换'
          case 'weixin':
            return '微信提现'
          case 'phone':
            return '手机充值'
          case 'alipay':
            return '支付宝提现'
        }
      }
    },

    components: {
      AnimateHeader
    },

    mounted () {
      if (this.$route.params.backroute === 'rebatescore') {
        this.result_info = '兑换申请已提交'
        this.button_text = '继续兑换'
      }
      if (this.$route.params.backroute === 'phone') {
        this.result_info = '充值申请已提交'
        this.button_text = '继续充值'
      }
    },

    methods: {
      back () {
        let backroute = '/withdraw/' + this.$route.params.backroute
        $router.back({
          path: backroute
        })
      },

      backHome () {
        $router.push('/dashboard')
      }
    }
  }
</script>



